@charset "utf-8";

// Define defaults for each variable.

$base-font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$base-font-size:   16px !default;
$base-font-weight: 400 !default;
$small-font-size:  $base-font-size * 0.875 !default;
$base-line-height: 1.5 !default;

$spacing-unit:     30px !default;

$text-color:            #bbbbbb !default;
$background-color:      #181818 !default;
//$brand-color-2:   #d784ee !default;
//$brand-color:      #a2c8f4 !default;
$brand-color: #82b3ef;
$brand-color-2: #e57373;
//$brand-color-2: #cea2f4;
$brand-color-3: darken($brand-color, 0%);
//$brand-color: #84eed7 !default;
$brand-color-light:     lighten($brand-color-2, 0%) !default;
$brand-color-dark:      darken($brand-color, 0%) !default;


$text-color:            #bbbbbb !default;
$background-color:      #181818 !default;
$code-background-color: #212121 !default;

$link-base-color:      darken($brand-color, 20%) !default;
$link-visited-color:    $link-base-color !default;
$link-hover-color:      $brand-color-light !default;

$site-title-color:      $brand-color-light !default;

//$grey-color:       $brand-color-dark !default;
//$grey-color-light: $brand-color-light !default;
//$grey-color-dark:  $brand-color !default;

$table-text-align: left !default;

// Width of the content area
$content-width:    800px !default;

$on-palm:          600px !default;
$on-laptop:        800px !default;

// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

@mixin relative-font-size($ratio) {
  font-size: $base-font-size * $ratio;
}

// Import partials.
@import
  "minima/base",
  "minima/layout",
  "minima/syntax-highlighting"
;
